@import url(https://fonts.googleapis.com/css?family=Rokkitt:700);
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
html, body, main {
    font-family: "Lucida Grande","Lucida Sans Unicode", Tahoma, Sans-Serif;
    position: relatiave;
    margin: 0;
    padding: 0;
    padding-top: 10px;
    width: 100%;
    height: 100%;
    background-color: #092B40;
}
input[type='text'] {
    -webkit-appearance: none;
    outline: none;
    border: none;
}
.wrapper {
    display: flex;
    width: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: box;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
    box-orient: vertical;
    box-direction: normal;
    -moz-flex-direction: column;
    flex-direction: column;
    -ms-flex-direction: column;
    box-align: center;
    -moz-align-items: center;
    -ms-align-items: center;
    -o-align-items: center;
    align-items: center;
    -ms-flex-align: center;
    box-pack: center;
    -moz-justify-content: center;
    -ms-justify-content: center;
    -o-justify-content: center;
    justify-content: center;
    -ms-flex-pack: center;
    -webkit-transition: all 2s linear;
    -o-transition: all 2s linear;
    transition: all 2s linear;
}
.fullHeight {
    height: 100%;
    -webkit-transition-duration: 2s;
    -o-transition-duration: 2s;
       transition-duration: 2s;
}
.white-text {
    color: white;
    text-decoration: none;
}
form {
    position: relative;
    display: block;
    z-index: 1;
    width: 40px;
    height: 40px;
    margin-left: 0;
    padding: 0;
    margin-bottom: 10px;
    border: 4px solid #D96F32;
    border-radius: 20px;
    -webkit-transition: all 0.25s ease 0.3s;
    -o-transition: all 0.25s ease 0.3s;
    transition: all 0.25s ease 0.3s;
    cursor: pointer;
}
form:before {
    top: 90%;
    left: 90%;
    width: 16px;
    height: 3px;
    background-color: #D96F32;
    border-radius: 1px;
    -webkit-transition: width 0.15s ease 0.55s;
    -o-transition: width 0.15s ease 0.55s;
    transition: width 0.15s ease 0.55s;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transform-origin: top left;
    -ms-transform-origin: top left;
    transform-origin: top left;
}
form input {
    width: 100%;
    height: 100%;
    padding: 0 30px 0 15px;
    font-size: 14px;
    line-height: 38px;
    opacity: 0;
    background-color: transparent;
    color: white;
    -webkit-transition: opacity 0.15s ease;
    -o-transition: opacity 0.15s ease;
    transition: opacity 0.15s ease;
}

.eks {
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    z-index: 20;
    width: 30px;
    height: 30px;
    cursor: pointer;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}
.eks:before, .eks:after {
    right: 5px;
    height: 2px;
    width: 2px;
    border-radius: 1px;
    -webkit-transition: all 0.25s ease;
    -o-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.eks:before {
    top: 0px;
    background-color: #D96F32;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-transform-origin: top right;
    -ms-transform-origin: top right;
    transform-origin: top right;
    -webkit-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
       transition-delay: 0.1s;
}
.eks:after {
    bottom: 0px;
    background-color: #D96F32;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transform-origin: bottom right;
    -ms-transform-origin: bottom right;
    transform-origin: bottom right;
    -webkit-transition-delay: 0s;
    -o-transition-delay: 0s;
       transition-delay: 0s;
}
form.open {
    width: 260px;
    -webkit-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
       transition-delay: 0.1s;
}
form.open:before {
    width: 0px;
    -webkit-transition-delay: 0s;
    -o-transition-delay: 0s;
       transition-delay: 0s;
}
form.open input {
    opacity: 1;
    -webkit-transition-delay: 0.15s;
    -o-transition-delay: 0.15s;
       transition-delay: 0.15s;
}
form.open .eks:before, form.open .eks:after {
    width: 15px;
    right: 12px;
}
form.open .eks:before {
    top: 9px;
    -webkit-transition-delay: 0.25s;
    -o-transition-delay: 0.25s;
       transition-delay: 0.25s;
}
form.open .eks:after {
    bottom: 9px;
    -webkit-transition-delay: 0.3s;
    -o-transition-delay: 0.3s;
       transition-delay: 0.3s;
}
.hide {
    display: none;
}
form:before, .eks:before, .eks:after {
    content: '';
    position: absolute;
    display: block;
}



